/* eslint-disable no-tabs */
<template>
  <div class="add">
    <div class="fleche">
      <router-link to="/livres"><i class="fas fa-arrow-left"></i></router-link>
    </div>
    <h3>Ajouter un livre</h3>
    <label class="spacing">Titre</label>
    <input name="titre" type="text">
    <label class="spacing">Auteur</label>
    <input name="auteur" type="text">
    <label class="spacing">Date de parution</label>
    <input name="dateParution" type="date">
    <label class="spacing">Couverture</label>
    <input name="couverture" type="file">
    <label class="spacing">Categorie(s)</label>
    <div class="row">
      <div class="check col-6 col-md-4 col-xl-3" v-for="categorie in categories" :key="categorie.id">
        <input type="checkbox" :id="categorie.id" :name="categorie.id">
        <label :for="categorie.id">{{categorie.name}}</label>
      </div>
    </div></br>
    <div class="radio">
      <div>
          <input name="type" type="radio" value="Acheté">
          <label for="Acheté">Acheté</label>
      </div>
      <div>
          <input name="type" type="radio" value="Emprunté">
          <label for="Emprunté">Emprunté</label>
      </div>
    </div>
    <label class="spacing">Date d'achat</label>
    <input name="dateAchat" type="date">

    <label class="spacing">A qui ?</label>
    <input name="aQui" type="text">

    <label class="spacing">Emplacement</label>
    <input class="spacing" name="piece" type="text" placeholder="Pièce">
    <input class="spacing" name="etagere" type="text" placeholder="Etagère">
    <input class="spacing" name="rangee" type="text" placeholder="Rangée">

    <button class="btn btn-dark" type="submit" @click="connect()">Valider</button>
  </div>
</template>

<script>
export default {
  name: 'AddLivreComp',
  data () {
    return {
      categories: [
        { id: '1', name: 'Drame' },
        { id: '2', name: 'Policier' },
        { id: '3', name: 'Science-fiction' },
        { id: '4', name: 'Fantasy' },
        { id: '5', name: 'Dystopie' },
        { id: '6', name: 'Psychologie' }
      ]
    }
  }
}
</script>

<style>
 .add{
    text-align: center;
    display: flex;
    flex-direction: column;
    margin-left: 20%;
    margin-right: 20%;
  }
  .spacing {
    margin-top: 1%;
    margin-bottom: 1%;
  }
  .button {
    margin-top: 20px;
  }
  .btn {
    margin-top: 3%;
    margin-bottom: 3%;
  }
  .radio, .check, .fleche {
      text-align: left;
  }
  @media (max-width: 575px) {
    .add {
        margin-left: 5%;
        margin-right: 5%;
    }
}

</style>


